<template>
  <!-- Main Content -->

  <div>
    <div class="" style="margin-top: -10px;" ref="timelineReview">
      <spinner :data="spinnerData"></spinner>
<!--        <all-feeds :feeds="reviews" :feed-status="feedsStatus" :feeds-count="fullFeedsCount"></all-feeds>-->
        <all-feeds :feeds="reviews" :feed-status="feedsStatus"></all-feeds>
    </div>
  </div>
  <!-- ... end Main Content -->
</template>

<script>
import {mapState} from "vuex";
import allFeeds from "../Home/allFeeds";
import ls from '../../localstorage';
var pageData = {
  reviews: [],
  allReviews: {
    next_page_url: '',
    last_page_url: '',
  },
  fullFeedsCount: '',
  photoReview: {},
  spinnerData: {},
  username: "",
  firstname: "",
  lastname: "",
    selectedFeed: '',
  feedsStatus: Boolean,
  avatar: "",
    signedIn: false,
};

export default {
  name: "Newsfeeds",
    components: {allFeeds},
    data() {
    return pageData;
  },
  watch: {
    $route(to) {
      this.setReviewOptions()
      return to;
    },
  },
  computed: {
    ...mapState(['user']),
    getUserDetails() {
      return this.user
    },
  },
  created() {
    this.setReviewOptions()
  },
  mounted() {
    window.eventBus.$on("loadFeeds", val => {
      if(val === true) {
        this.setReviewOptions()
      }
    })
  },
  methods: {
    showLoading(options = {}) {
      let loader = this.spinnerData = {
        spinner: 'Jumper',
        isLoading: options.show !== undefined ? options.show : true ,
        color: '#00bfff',
        size: '60px',
        options: 'center relative',
      };
      return loader;
    },
    setReviewOptions() {
      const userDV = this.getUserDetails;
      const userLC = ls.getStorage('fansData')[0];
      let userDetails = '';
      if (userDV) {
        userDetails = userDV
      }else {
        userDetails = userLC
      }

      if (userLC.id) {
        this.signedIn = true
        this.getReviews(userLC)
      }else {
        this.signedIn = false
        this.getOtherUsers()
      }
    },
    getReviews(userDetails) {
      let usernameRoute = this.$route.params.username;
      if (userDetails.username === usernameRoute) {
        this.getAllReviews()
      }else {
        this.getOtherUsers()
      }
    },
    getOtherUsers() {
      this.showLoading();
      let usernameRoute = this.$route.params.username;
      this.reviews = [];
      this.allReviews = {};
      this.axios
        .get("/other/detailsout/" + usernameRoute + "/100")
        .then(response => {
          this.showLoading({show:false})
          var responseData = response.data.data;
          // console.log("profile reviews",response);
          if (responseData.reviews.data.length > 0) {
            this.feedsStatus = true
          }else {
            this.feedsStatus = false
          }
          // console.log(responseData.reviews.data)
          this.allReviews = {};
          this.reviews.push(...responseData.reviews.data);

          this.reviews.fullFeedsCount = responseData.reviews.total;
          this.allReviews.next_page_url = responseData.reviews.next_page_url;
          this.allReviews.last_page_url = responseData.reviews.last_page_url;

          this.username = responseData.username;
          this.firstname = responseData.firstname;
          this.lastname = responseData.lastname;
          this.avatar = responseData.avatar;
        })
    },
    getAllReviews() {
      this.showLoading();
      let usernameRoute = this.$route.params.username;

      this.allReviews = {};
      this.reviews = [];
      this.axios
        .get("/other/detailsin/" + usernameRoute + "/100")
        .then(response => {
          this.showLoading({show:false})
          var responseData = response.data.data;
          // console.log("profile reviews",responseData);
          if (responseData.reviews.data.length > 0) {
            this.feedsStatus = true
          }else {
            this.feedsStatus = false
          }
          this.allReviews = {};
          this.reviews.push(...responseData.reviews.data);

          this.reviews.fullFeedsCount = responseData.reviews.total;
          this.allReviews.next_page_url = responseData.reviews.next_page_url;
          this.allReviews.last_page_url = responseData.reviews.last_page_url;

          this.username = responseData.username;
          this.firstname = responseData.firstname;
          this.lastname = responseData.lastname;
          this.avatar = responseData.avatar;
        })
	},
  },
};
</script>

